import React, { useState, useEffect, useTran } from 'react';
import './App.css';
import M from "materialize-css/dist/js/materialize.min.js";
import {scrollToEle} from './helpers/helperFuncs';
import Scrollspy from './SideBar/ScrollSpy';

const listArr = ['about', 'experience', 'projects', 'skills', 'contacts']


export const Navbar = (props) => {
    let matchesQuery = props.matchesQuery;
    const [activeIdx, setActiveIdx] = useState(null);
    useEffect(() => {
        var sidenav = document.querySelectorAll(".sidenav");
        M.Sidenav.init(sidenav, {});

            window.yPositionArr = [
            document.getElementById('about').offsetTop+document.getElementById('about').offsetHeight,
            document.getElementById('experience').offsetTop+document.getElementById('experience').offsetHeight,
            document.getElementById('projects').offsetTop+document.getElementById('projects').offsetHeight,
            document.getElementById('skills').offsetTop+document.getElementById('skills').offsetHeight,
            document.getElementById('contacts').offsetTop+document.getElementById('contacts').offsetHeight,
            ]
            console.log(window.yPositionArr)

        window.addEventListener('scroll', function(e) {
            const yPos = window.pageYOffset;
            const yArr = window.yPositionArr;
            let result = 0;
            for(let i in yArr){
                if(yPos > yArr[i]){
                    continue;
                }
                else{
                    result = i;
                    break;
                }
            }
            setActiveIdx(result)
            

        })
    }, []);


    return (
        <div>
            {/* {matchesQuery === 'mobile' ?
                <div className="navbar-fixed">
                    <nav className="teal darken-3">
                        <div className="nav-wrapper mx-4">
                            <a href="#!" className="brand-logo black-text">
                                JIHYEON JEONG
                            </a>
                            <a href="#!" className="sidenav-trigger" data-target="mobile-nav">
                                <i className="material-icons">menu</i>
                            </a>

                        </div>
                    </nav>
                </div>
                : null
            } */}

            <nav >

    
                <ul className="sidenav sidenav-fixed" id="mobile-nav" >
                    <div class='valign-wrapper' style={{justifyContent: 'center'}}>
                      <img src={require('./images/0.jpg')} class='center-align' style={{ height: 150, borderRadius: 100, marginTop: 20}} />
                    </div>
                    <li>
                        <div className="divider blue-grey darken-2" />
                    </li>
                    {listArr.map((item, idx) => {
                        return (
                                <li>
                                  <a 
                                  href={`#${item}`} 
                                  className="teal-text  menu-item" 
                                  style={activeIdx === idx ? {backgroundColor: 'tan'} : {}}
                                  id={`#${item}`} 
                                  onClick={(e)=>{
                                    scrollToEle(e, item)
                                    }}>
                                    {item}
                                  </a>
                                </li>
                            )
                    })}
                    {/* <li>
                        <a href="#about" id='#about' className="teal-text waves-effect waves-green menu-item" onClick={(e)=>{
                            scrollToEle(e, 'about')
                            
                            }}>
                            about
                        </a>
                    </li>
                    <li>
                        <a href="#experience" className="teal-text waves-effect waves-green menu-item" id='#experience' onClick={(e)=>{
                            scrollToEle(e, 'experience')
                            
                            }}>
                            experience
                        </a>
                    </li>
                    <li>
                        <a href="#projects" className="teal-text waves-effect waves-green menu-item" id='#project' onClick={(e)=>{
                            scrollToEle(e, 'projects')
                            
                            }}>
                            projects
                        </a>
                    </li>
                    <li>
                        <a href="#skills" className="teal-text waves-effect waves-green menu-item" id='#skills' onClick={(e)=>{
                            scrollToEle(e, 'skills')
                            
                            }}>
                            skills
                        </a>
                    </li>

                    <li>
                        <a href="#contacts" className="teal-text waves-effect waves-green menu-item" id='#contacts' onClick={(e)=>{
                            scrollToEle(e, 'contacts')
                            
                            }}>
                            contact
                        </a>
                    </li> */}

                    <li>
                        <a href="" className="teal-text waves-effect menu-item" onClick={(e)=>{
                            e.preventDefault();
                            window.open('https://1drv.ms/w/s!As-xro1pkHMwgbY3uXt9YABVXf7sLA?e=KtHfIb')
                        }}>
                            resume
                        </a>
                    </li>
                </ul>

            </nav>
            {/* <nav class='hide-on-large-only' >
                <ul className="sidenav sidenav-fixed" id="mobile-nav" >
                    <div class='valign-wrapper' style={{justifyContent: 'center'}}>
                      <img src={require('./images/0.jpg')} class='center-align' style={{ height: 150, borderRadius: 100, marginTop: 20}} />
                    </div>
                    <li>
                        <div className="divider blue-grey darken-2" />
                    </li>
                    <li>
                        <a href="profiles.html" className="cyan-text">
                            about
                        </a>
                    </li>
                    <li>
                        <a href="register.html" className="cyan-text">
                            experience
                        </a>
                    </li>
                    <li>
                        <a href="login.html" className="cyan-text">
                            projects
                        </a>
                    </li>
                    <li>
                        <a href="login.html" className="cyan-text">
                            education
                        </a>
                    </li>
                    <li>
                        <a href="login.html" className="cyan-text">
                            contact
                        </a>
                    </li>
                    <li>
                        <a href="login.html" className="cyan-text">
                            blog
                        </a>
                    </li>
                    <li>
                        <a href="login.html" className="cyan-text">
                            resume
                        </a>
                    </li>
                </ul>
            </nav> */}
            
        </div>
    );
};
export default Navbar;


